import { IconButton } from "@mui/material";
import React, { useEffect, useRef, useState } from "react";
import { NavLink, useLocation } from "react-router-dom";
import { setSection } from "../slices/SectionSlice";
import { useDispatch, useSelector } from "react-redux";
import { Section } from "../constants/Section";
import { useNavigate } from "react-router-dom";

const Header = () => {
  const navigate = useNavigate();
  const location = useLocation();

  const dispatch = useDispatch();
  const section = useSelector((state) => state.section.name);

  const navbarRef = useRef(null);

  const [toggleMobileMenu, setToggleMobileMenu] = useState(false);
  const locationSection = location?.hash?.replace("#", "") || "home";

  useEffect(() => {
    try {
      if (locationSection) {
        dispatch(setSection(locationSection));
      }
    } catch (e) {}
  }, []);

  useEffect(() => {
    navigate(section && section !== Section.HOME.id ? `#${section}` : "/");
  }, [section]);

  useEffect(() => {
    if (toggleMobileMenu) {
      document.addEventListener("click", handleOutsideClick);
    }
    return () => {
      document.removeEventListener("click", handleOutsideClick);
    };
  }, [toggleMobileMenu]);

  const handleOutsideClick = (e) => {
    if (
      navbarRef?.current &&
      !navbarRef?.current?.contains(e.target) &&
      e?.target?.nodeName?.toLowerCase() !== "img" &&
      toggleMobileMenu
    ) {
      setToggleMobileMenu(false);
    }
  };

  const handleNavLinkClick = (section) => {
    dispatch(setSection(section));
  };

  const isSectionActive = (sectionName = null) =>
    sectionName === locationSection;

  return (
    <div className="header">
      <div className="navbar-container">
        <h1
          onClick={() => {
            setToggleMobileMenu(false);
            handleNavLinkClick(Section.HOME.id);
          }}
          className="navbar-name"
        >
          Branko M.
        </h1>
        <div
          ref={navbarRef}
          className={`navbar-links-container ${toggleMobileMenu && "active"}`}
        >
          <div
            onClick={() => {
              setToggleMobileMenu(false);
              handleNavLinkClick(Section.HOME.id);
            }}
            className={`navbar-link ${
              isSectionActive() || (isSectionActive("home") && "active")
            }`}
          >
            Home
          </div>
          <div
            onClick={() => {
              setToggleMobileMenu(false);
              handleNavLinkClick(Section.ABOUT_ME.id);
            }}
            className={`navbar-link ${
              isSectionActive(Section.ABOUT_ME.id) && "active"
            }`}
          >
            About Me
          </div>
          <div
            onClick={() => {
              setToggleMobileMenu(false);
              handleNavLinkClick(Section.PROJECTS.id);
            }}
            className={`navbar-link ${
              isSectionActive(Section.PROJECTS.id) && "active"
            }`}
          >
            Projects
          </div>
          <div
            onClick={() => {
              setToggleMobileMenu(false);
              handleNavLinkClick(Section.CONTACT_ME.id);
            }}
            className={`navbar-link ${
              isSectionActive(Section.CONTACT_ME.id) && "active"
            }`}
          >
            Contact Me
          </div>

          {/* <NavLink
            to="/Branko_Milovanovic_RESUME.pdf"
            target="_blank"
            download
            onClick={() => setToggleMobileMenu(false)}
            className="navbar-link"
          >
            Download Resume
          </NavLink> */}
        </div>
        <div className="hamburger">
          <IconButton
            onClick={() => setToggleMobileMenu((prevState) => !prevState)}
          >
            <img
              src={
                !toggleMobileMenu
                  ? "/assets/images/hamburger-menu.svg"
                  : "/assets/images/close.svg"
              }
              style={{
                transition: "transform 0.3s ease",
                transform: toggleMobileMenu && "rotate(180deg)",
              }}
            />
          </IconButton>
        </div>
      </div>
    </div>
  );
};

export default Header;
