import React from "react";
import { Section } from "../constants/Section";
import Delimiter from "./Delimiter";
import { Controller, useForm } from "react-hook-form";
import { Button } from "@mui/material";
import emailjs from "emailjs-com";
import { useSnackbar } from "notistack";

const formRules = {
  email: {
    required: { value: true, message: "This field is required!" },
    pattern: {
      value:
        /^(([^<>()[\]\\.,;:\s@"]+(\.[^<>()[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/,
      message: "E-mail is not in a valid format! (e.g mail@example.com)",
    },
  },
  message: {
    required: { value: true, message: "This field is required!" },
  },
};

const ContactMeSection = () => {
  const { enqueueSnackbar } = useSnackbar();

  const form = useForm({
    mode: "onBlur",
    defaultValues: {
      email: "",
      subject: "",
      message: "",
    },
  });

  const {
    control,
    handleSubmit,
    formState: { errors },
  } = form;

  const onSubmit = (data) => {
    enqueueSnackbar("Message is being sent...", {
      variant: "info",
      autoHideDuration: 3000,
    });

    const templateParams = {
      ...data,
      subject: data?.subject || "New Message from Your Portfolio Website",
    };

    emailjs
      .send(
        process.env.REACT_APP_EMAIL_JS_SERVICE_ID,
        process.env.REACT_APP_EMAIL_JS_TEMPLATE_ID,
        templateParams,
        process.env.REACT_APP_EMAIL_JS_USER_ID
      )
      .then(() => {
        enqueueSnackbar("Message successfully sent, thank you ", {
          variant: "success",
          autoHideDuration: 5000,
        });
        form.reset();
      })
      .catch((err) => {
        console.warn(err);
        enqueueSnackbar(
          "Unfortunately, the message couldn't be sent (please try again or send it manually) ",
          {
            variant: "error",
            autoHideDuration: 5000,
          }
        );
      });
  };

  return (
    <section id={Section.CONTACT_ME.id} className="contact-section">
      <div className="content-container">
        <Delimiter style={{ marginBottom: "5px" }} />
        <h1>Contact Me</h1>
        <div className="wrapper-forms">
          <div className="contact-forms">
            <div className="contact-form">
              <h1>Get in touch</h1>
              <Controller
                name={"email"}
                control={control}
                rules={formRules.email}
                render={({ field: { onChange, value } }) => (
                  <div className="form-group">
                    <label>E-Mail:</label>
                    <input
                      type="email"
                      placeholder="Enter your email"
                      value={value}
                      onChange={(e) => onChange(e?.target?.value)}
                    />
                    {Boolean(errors?.email) && (
                      <p className="error-message">{errors?.email?.message}</p>
                    )}
                  </div>
                )}
              />
              <Controller
                name={"subject"}
                control={control}
                render={({ field: { onChange, value } }) => (
                  <div className="form-group">
                    <div
                      style={{
                        display: "flex",
                        alignItems: "center",
                        gap: "5px",
                      }}
                    >
                      <label>Subject</label>
                      <span style={{ fontSize: 12 }}>(optional):</span>
                    </div>
                    <input
                      type="text"
                      placeholder="Enter the subject"
                      value={value}
                      onChange={(e) => onChange(e?.target?.value)}
                    />
                  </div>
                )}
              />
              <Controller
                name={"message"}
                control={control}
                rules={formRules.message}
                render={({ field: { onChange, value } }) => (
                  <div className="form-group">
                    <label>Message:</label>
                    <textarea
                      placeholder="Enter your message"
                      value={value}
                      rows={5}
                      onChange={(e) => onChange(e?.target?.value)}
                    />
                    {Boolean(errors?.message) && (
                      <p className="error-message">
                        {errors?.message?.message}
                      </p>
                    )}
                  </div>
                )}
              />
              <Button onClick={handleSubmit(onSubmit)}>Send</Button>
            </div>
            <div className="personal-info">
              <h1>Personal Info</h1>
            </div>
          </div>
        </div>
      </div>
    </section>
  );
};

export default ContactMeSection;
