import { Route, Routes, Navigate } from "react-router-dom";
import MainPage from "./pages/MainPage";

export const ROUTES = {
  Main: {
    path: "/",
    component: <MainPage />,
  },
};

export function getRoutes() {
  return (
    <Routes>
      {Object.entries(ROUTES).map(([key, value]) => (
        <Route
          key={"route-" + key}
          path={value.path}
          element={value.component}
        />
      ))}
    </Routes>
  );
}
