import { LinearProgress } from "@mui/material";
import React, { useEffect, useRef, useState } from "react";
import { TypeAnimation } from "react-type-animation";
import { Section } from "../constants/Section";

const HomeSection = () => {
  const progressRef = useRef(null);

  const [progress, setProgress] = useState(0);
  const [buffer, setBuffer] = useState(10);

  useEffect(() => {
    progressRef.current = () => {
      const diff = Math.random() * 10;
      const diff2 = Math.random() * 10;
      setProgress(progress + diff);
      setBuffer(progress + diff + diff2);
    };

    if (progress > 100 && buffer > 100) {
      setProgress(0);
      setBuffer(10);
    }

    const timer = setInterval(() => {
      progressRef.current();
    }, 500);

    return () => {
      clearInterval(timer);
    };
  }, [progress]);

  return (
    <section id={Section.HOME.id}>
      <div className="content-container">
        <div style={{ position: "static", zIndex: -1, textAlign: "center" }}>
          <span style={{ fontSize: "24px" }}>{`Hi, I'm a `}</span>
          <TypeAnimation
            sequence={[
              "Branko Milovanovic",
              3000,
              "Web Developer",
              3000,
              "Full Stack Developer",
              3000,
            ]}
            wrapper="span"
            style={{ fontSize: "24px", color: "rgb(228, 228, 14)" }}
            speed={5}
            deletionSpeed={20}
            repeat={Infinity}
          />
        </div>

        <span style={{ fontSize: "24px", textAlign: "center" }}>
          Welcome to my portfolio website
        </span>
        <div style={{ width: "80%", marginTop: "50px", zIndex: "-1" }}>
          <span style={{ fontSize: "24px" }}>Working in progress...</span>

          <LinearProgress
            variant="buffer"
            value={progress}
            valueBuffer={buffer}
          />
        </div>
      </div>
    </section>
  );
};

export default HomeSection;
