import React, { useEffect } from "react";
import { useSelector } from "react-redux";
import { Section, getSections } from "../constants/Section";
import { useNavigate } from "react-router-dom";

const animations = [
  {
    className: "background",
  },
  {
    className: "background",
    style: {
      animationDirection: "alternate-reverse",
      animationDuration: "8s",
    },
  },
  {
    className: "background",
    style: {
      animationDuration: "5s",
    },
  },
];

const MainPage = () => {
  const navigate = useNavigate();

  const section = useSelector((state) => state.section.name);

  useEffect(() => {
    scrollToSection(section);
  }, [section]);

  const scrollToSection = (sectionName) => {
    document
      .getElementById(sectionName)
      ?.scrollIntoView({ behavior: "smooth" });
  };

  const renderBackgroundAnimations = () => {
    return (
      <>
        {animations.map((animation, index) => (
          <div
            key={index}
            className={animation?.className}
            style={animation?.style}
          />
        ))}
      </>
    );
  };

  const isSectionInViewport = (section) => {
    const rect = section.getBoundingClientRect();
    return (
      rect.top >= 0 &&
      rect.bottom <=
        (window.innerHeight || document.documentElement.clientHeight)
    );
  };

  const handleOnScroll = (e) => {
    for (let section of document.querySelectorAll("section")) {
      const sectionName = section?.getAttribute("id");
      if (isSectionInViewport(section)) {
        navigate(
          sectionName && sectionName !== Section.HOME.id
            ? `#${sectionName}`
            : ""
        );
      }
    }
  };

  return (
    <div className="main-container" onScroll={handleOnScroll}>
      {renderBackgroundAnimations()}
      <div className="social-buttons">
        <a
          href="https://www.linkedin.com/in/branko-milovanovic/"
          target="_blank"
          rel="noreferrer"
        >
          <img
            src={"/assets/images/linkedin.svg"}
            className="social-icon linkedin"
            alt="LinkedIn"
          />
        </a>
        <a
          href="https://github.com/brankomilovanovic"
          rel="noreferrer"
          target="_blank"
        >
          <img
            src={"/assets/images/github.svg"}
            className="social-icon github"
            alt="GitHub"
          />
        </a>
      </div>
      {getSections()}
    </div>
  );
};

export default MainPage;
